export default "@import '../../themes/src/default.css';\n\n:root {\n    --spinner-display-visible: inline-block;\n    --spinner-animation-duration: 0.8s;\n    --spinner-animation-timing-function: linear;\n}\n\n.spinner {\n    display: none;\n    animation: spin_animation var(--spinner-animation-duration) infinite\n        var(--spinner-animation-timing-function);\n}\n\n.visible {\n    display: var(--spinner-display-visible);\n    vertical-align: middle;\n}\n\n.xs {\n    width: 18px;\n    height: 18px;\n}\n\n.s {\n    width: 24px;\n    height: 24px;\n}\n\n.m {\n    width: 48px;\n    height: 48px;\n}\n\n@keyframes spin_animation {\n    from {\n        transform: rotate(0);\n    }\n\n    to {\n        transform: rotate(360deg);\n    }\n}\n";